<template lang="pug">
    #app
        #top
            h2 Setback area calculator
        #middle
            #sidebar
                #setbacks
                    h3 Define the setbacks
                    .setback(v-for="setback in setbacks")
                        | &rarr;
                        input.horizontal(v-model.number="setback.x")
                        | m&nbsp; &nbsp;&nbsp;
                        | Height: 
                        input.vertical(v-model.number="setback.h")
                        | m
                    //- .setback
                        | Default vertical
                        input.vertical
                    br
                    .min-floor-area
                        label Min. area per floor &nbsp;
                        input(v-model.number="minFloorArea")
                        | m²
                    br
                    button#recalculate(v-on:click="recalculate()") Recalculate!
                #total(v-if="floorArea")
                    h2 Total floor area
                    .floor(v-for="(area, floor) in floorArea.floors")
                        span.floor-num {{ floor ? 'Floor ' + floor : 'Ground' }}: 
                        span.floor-area {{  Math.round(area).toLocaleString() }} m²
                    .floor-total
                        span.floor-num Total
                        span.floor-area {{  Math.round(floorArea.total).toLocaleString() }} m²

            #map
        #bottom
            | Frontier SI
</template>

<script>

export default {
    name: 'app',
    components: {

    }, data() {
        return {
            floorArea: undefined,
            setbacks: [
                { x: 2, h: 0 },
                { x: 3, h: 5 },
                { x: 4, h: 15 },
                { h: 30 }        
            ], minFloorArea: 100//, precinct: undefined
        }
    }, computed: {
        setbackFields() {

        }
    }, watch: {
        setbacks(newSetbacks) {
            precinctChanged()
        }
    }, methods: {
        recalculate() {
            precinctChanged()
        }
    }, mounted() {
        window.app = this;
    }
}

const {fetch} = require('whatwg-fetch');
const Pbf = require('pbf');
const geobuf = require('geobuf');
const mapboxgldraw = require('@mapbox/mapbox-gl-draw');
const mapboxgl = require('mapbox-gl');
const turf = require('@turf/turf'); // TODO use only the modules we need
const makeZigguraut = require('./makeZigguraut');

// import jamsession from '@mapbox/expression/jamsession';
const jam = require('@mapbox/expression-jamsession').formulaToExpression;
function updateParcelSelection(parcels, precinct) {
    let selectedCount = 0;
    turf.featureEach(parcels, (parcel, id) => {
        let selected = precinct ? !turf.booleanDisjoint(parcel, precinct) : false;
        map.setFeatureState({
            id: id,
            source: 'parcels'
        },{
            selected: selected
        });
        parcel._selected = selected;
        selectedCount += selected ? 1 : 0;
    });
    console.log(selectedCount + ' parcels found within precinct');
}


function precinctChanged(e) {
    const precinct = turf.combine(drawControl.getAll()); // multiple polygons to a single multipolygon
    if (precinct) {
        console.log(''+precinct.features[0].geometry.coordinates[0].length-1 + ' vertices.');
    }
    updateParcelSelection(parcels, precinct);
    const zigguraut = makeZigguraut.makeZigguraut(parcels, window.app.setbacks, window.app.minFloorArea);
    map.getSource('ziggurat').setData(zigguraut);
    window.app.floorArea = makeZigguraut.totalFloorArea(zigguraut);
    // document.getElementById('total-floor-area').innerHTML = Math.round(setbacks.totalFloorArea(ziggurat)) + ' square metres';
}
let drawControl, parcels, map;
function initMap(parcels) {
    mapboxgl.accessToken = 'pk.eyJ1Ijoic3RldmFnZSIsImEiOiJGcW03aExzIn0.QUkUmTGIO3gGt83HiRIjQw';
    map = new mapboxgl.Map({
        container: 'map', // container id
        style: 'mapbox://styles/mapbox/streets-v9', // stylesheet location
        center: [145.2726, -37.816], // starting position [lng, lat]
        bearing: 68,
        // pitch: 57,
        zoom: 15 // starting zoom
    });
    map.onLoad = cb => map.loaded() ? cb() : map.on('load', cb);
    map.onLoad(() => {
        map.addSource('parcels', {
            type: 'geojson',
            data: parcels,
            generateId: true
        });
        map.addLayer({
            id: 'parcels-outline',
            source: 'parcels',
            type: 'line',
            paint: {
                'line-color': 'hsl(240,50%,80%)',
            }
        })
        // map.addLayer({
        //     id: 'parcels-selected',
        //     source: 'parcels',
        //     type: 'fill',
        //     paint: {
        //         'fill-color': 'red'
        //     },
        //     filter: ['<', ['id'], 5000]
        // })
        map.addLayer({
            id: 'parcels-selected',
            source: 'parcels',
            type: 'fill',
            paint: {
                'fill-color':
                    ['case',
                        ['to-boolean', ['feature-state', 'selected']], 'darkgrey',
                        'transparent'
                    ]
            },
            
        })
        map.addSource('ziggurat', {
            type: 'geojson',
            data: turf.featureCollection([]) //'zdata/zig.json'
        });
        map.addLayer({
            id: 'heights',
            type: 'fill-extrusion',
            source: 'ziggurat',
            paint: {
                'fill-extrusion-color': {
                    //'hsl(180, 80%, 50%)',
                    property: 'height',
                    stops: [
                        [0, 'hsl(100, 60%,40%)'],
                        [1, 'hsl(280, 95%,60%)'],
                        [30, 'hsl(240, 95%,80%)']
                    ]
                },
                'fill-extrusion-opacity': 0.9,
                'fill-extrusion-height': {
                    type: 'identity',
                    property: 'height'
                }

            }
        });
    });
    drawControl = new mapboxgldraw({
        displayControlsDefault: false,
        controls: {
            polygon: true,
            trash: true
        }
    });
    map.addControl(drawControl);
    map.on('draw.create', precinctChanged);
    map.on('draw.delete', precinctChanged);
    map.on('draw.update', precinctChanged);

}

// initMap();

fetch('data/out.geobuf')
    .then(response => {
        return response.arrayBuffer()
    })
    .then(buffer => {
        parcels = geobuf.decode(new Pbf(buffer));
        initMap(parcels);      
    });






</script>
